import React, {Component} from 'react';

import ReactHowler from 'react-howler'

import Footer from '../footer';
import Header from '../header';

import {backgroundImage} from '../../helpers/helpers';

import bg_cream from '../../sources/img/backgrounds/cream_dust.png';

import bg_01 from '../../sources/img/sound-mixer/bg_01.jpg';
import bg_02 from '../../sources/img/sound-mixer/bg_02.jpg';
import bg_03 from '../../sources/img/sound-mixer/headphones.jpg';

import Forest_Example from '../../sources/sounds/sound-mixer/forest_example.ogg';
import Urban_Example from '../../sources/sounds/sound-mixer/city_example.ogg';
import Lab_Example from '../../sources/sounds/sound-mixer/lab_example.ogg';

import hero_bg from '../../sources/img/sound-mixer/bg.jpg';

const PLAYING_TYPES = {FOREST: "forest", URBAN:"urban", LAB:"lab"};


class SoundMixer extends Component {
  constructor(props){
    super(props);

    this.toggleFamiliarState = this.toggleFamiliarState.bind(this);
    this.displayIfFamiliar = this.displayIfFamiliar.bind(this);
    this.displayIfUnfamiliar = this.displayIfUnfamiliar.bind(this);

    this.onMouseEnterForest = this.onMouseEnterForest.bind(this);
    this.onMouseEnterUrban = this.onMouseEnterUrban.bind(this);
    this.onMouseEnterLab = this.onMouseEnterLab.bind(this);

    this.onMouseLeaveForest = this.onMouseLeaveForest.bind(this);
    this.onMouseLeaveUrban = this.onMouseLeaveUrban.bind(this);
    this.onMouseLeaveLab = this.onMouseLeaveLab.bind(this);

    this.soundsOnMouse = this.soundsOnMouse.bind(this);

    this.state={
      familiar: false,
      forest:false,
      urban:false,
      lab:false
    }
  }

  toggleFamiliarState(){
    console.log("Toggling")
    this.setState({familiar: !this.state.familiar});
  }

  onMouseEnterForest(){this.soundsOnMouse(PLAYING_TYPES.FOREST, true)}
  onMouseEnterUrban(){this.soundsOnMouse(PLAYING_TYPES.URBAN, true)}
  onMouseEnterLab(){this.soundsOnMouse(PLAYING_TYPES.LAB, true)}

  onMouseLeaveForest(){this.soundsOnMouse(PLAYING_TYPES.FOREST, false)}
  onMouseLeaveUrban(){this.soundsOnMouse(PLAYING_TYPES.URBAN, false)}
  onMouseLeaveLab(){this.soundsOnMouse(PLAYING_TYPES.LAB, false)}

  soundsOnMouse(type, enter){
    console.log("CALLED");
    this.setState({[type]:enter});
  }
  displayIfFamiliar(){
    if(!this.state.familiar){
      return {display:'none'};
    }
    return {};
  }
  displayIfUnfamiliar(){
    if(this.state.familiar){
      return {display:'none'};
    }
    return {};
  }
  render(){

    const BG_01 = backgroundImage(bg_01);
    const BG_02 = backgroundImage(bg_02);
    const BG_03 = backgroundImage(bg_03);

    const BG_Cream = backgroundImage(bg_cream);
    const Hero_BG = backgroundImage(hero_bg);

    return(
      <div className="sound-mixer">
        <Header sticky back/>
        <section className="hero-50 section dark sound-mixer-hero">
          <i className="background opacity-4 top" style={Hero_BG}/>
          <div className="container v-center">
            <div className="v-center-child">
              <h1>Sound-Mixer</h1>
            </div>
          </div>
        </section>
        <section className="section p-none">
          <div className="container-fluid">
            <div className="row">
              <div className="col-12">
                <section className="section bg-none">
                  <div className="row justify-content-around">
                    <div className="col-12 col-md-4 col-lg-3 col-xl-2 d-none d-md-block">
                      <div className="built-with">
                        <h4>Built With</h4>
                        <ul>
                          <li><p>React</p></li>
                          <li><p>Redux</p></li>
                          <li><p>HTML5</p></li>
                          <li><p>SASS/CSS</p></li>
                          <li><p>Javascript/jQuery</p></li>
                          <li><p>Bootstrap</p></li>
                        </ul>
                      </div>
                    </div>
                    <div className="col-11 col-md-8 col-xl-7">
                      <h1 className="weight-300">Soundscape Builder</h1>
                      <div className="built-with-mobile d-md-none">
                        <ul>
                          <li><p>React</p></li>
                          <li><p>Redux</p></li>
                          <li><p>HTML5</p></li>
                          <li><p>SASS/CSS</p></li>
                          <li><p>Javascript/jQuery</p></li>
                          <li><p>Bootstrap</p></li>
                        </ul>
                      </div>
                      <p>This app allows a user to build complex soundscapes using their own sound files, along with a library of sound files provided in the app. These soundscapes can be saved, loaded, and played in real-time, allowing the user to dynamically change the soundscape as it plays.</p>
                      <p>The app is primarily targeted at individuals running Tabletop Rollplaying Games (T-RPGs), to allow them (and myself), greater control over the soundscapes and music in their game.</p>
                      <p>This kind of thing is something better seen. I would love if you checked out an example video, and maybe play around with the app.</p>
                      <a className="button-small-thin-outline-dark m-l-0" href="https://DougWatro.com/sound-mixer">Check Out The App</a>
                      <a className="button-small-thin-outline-dark disabled" href="#video-example">Watch An Example</a>
                    </div>
                  </div>
                </section>
              </div>
            </div>
          </div>
        </section>
        <section className="section" id="video-example">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <div className="video-wrapper">
                  <iframe src="https://www.youtube-nocookie.com/embed/I0ESAjfzLz8" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen className="youtube-video"></iframe>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section className="section p-none">
          <div className="container-fluid">
            <div className="row">
              <div className="col-lg-7">
                <section className="section bg-none">
                  <div className="row justify-content-center">
                    <div className="col-10 col-lg-7">
                      <h2 className="weight-300">What's a Tabletop RPG?</h2>
                      <p>Tabletop RPGs are like boardgames with stories. You sit around a table and your Game Master (The GM) builds for you, in the theater of your mind, a story, called a "Campaign".</p>
                      <p>Players, acting as their characters, work through that story as protagonists, where the actions they take and the way they engage with the world will ultimately lead to an organic conclusion.</p>
                      <p>A Tabletop RPG is still very much a game, with books of rules the size of encyclopedias, but with an added flair of performative improvisation.</p>
                      <p>They are a unique form of story telling, where immersion into the story's universe is essential, and anything that can facilitate that is always a welcomed addition.</p>
                      <h4 className="color-accent italic">It is widely agreed that <span className="weight-600">sound</span> is a fantastic way to create immersion.</h4>
                    </div>
                  </div>
                </section>
              </div>
              <div className="col-lg-5 dark bg-dark-1 roll20-quote">
                <i className="background opacity-3" style={BG_01}/>
                <div className="v-center">
                  <div className="v-center-child">
                    <blockquote cite="https://wiki.roll20.net/Introduction_To_Tabletop_RPGs" className="tabletop-quote">
                    <a href="https://wiki.roll20.net/Introduction_To_Tabletop_RPGs">"The barebones essence of a Tabletop RPG is collaborative storytelling. It’s a story co-authored by everyone sitting at the table while playing the game."
                    <span className="attribution">Roll 20, Intro To Tabletop RPGs</span></a>
                    </blockquote>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section className="section py-5 dark">
        <i className="background hard-light opacity-1 top" style={BG_02}></i>
          <div className="container-fluid">
            <div className="row">
              <div className="col-lg-5 col-12">
                <div className="v-center">
                  <section className="section bg-none v-center-child pt-5 pb-3 pb-lg-5">
                    <div className="row justify-content-center">
                      <div className="col-md-9 col-10">
                        <h1 className="weight-300">What Is A Soundscape?</h1>
                        <p>Lets define a "Soundscape" as a collection of audio that, when played together, evokes the image or schema of place or time.</p>
                        <p className="italic">Scroll over or Tap the following to hear examples of soundscapes made in this app.</p>
                      </div>
                    </div>
                  </section>
                </div>
              </div>
              <div className="col-lg-7 col-12 dark bg-none">
                <div className="v-center">
                  <section className="section bg-none v-center-child pb-5 pt-5 pt-lg-5">
                    <div className="row justify-content-center">
                      <div className="col-4">
                        <div className="example-item" onMouseEnter={this.onMouseEnterForest} onMouseLeave={this.onMouseLeaveForest} >
                          <i className="fas fa-tree"></i>
                          <p>Nature</p>
                          <ul>
                            <li><p>Birds</p></li>
                            <li><p>Rustling Leaves</p></li>
                            <li><p>Wind</p></li>
                          </ul>
                          <ReactHowler
                           src={Forest_Example}
                           playing={this.state.forest}
                           loop={true}
                           />
                        </div>
                      </div>
                      <div className="col-4">
                        <div className="example-item" onMouseEnter={this.onMouseEnterUrban} onMouseLeave={this.onMouseLeaveUrban}>
                          <i className="fas fa-city"></i>
                          <p>Urban Center</p>
                          <ul>
                            <li><p>Traffic</p></li>
                            <li><p>Construction</p></li>
                            <li><p>People</p></li>
                          </ul>
                          <ReactHowler
                           src={Urban_Example}
                           playing={this.state.urban}
                           loop={true}
                           />
                        </div>
                      </div>
                      <div className="col-4">
                        <div className="example-item" onMouseEnter={this.onMouseEnterLab} onMouseLeave={this.onMouseLeaveLab}>
                          <i className="fas fa-flask"></i>
                          <p>Laboratory</p>
                          <ul>
                            <li><p>Buzzing Lights</p></li>
                            <li><p>Machines</p></li>
                            <li><p>Computers</p></li>
                          </ul>
                          <ReactHowler
                           src={Lab_Example}
                           playing={this.state.lab}
                           loop={true}
                           />
                        </div>
                      </div>
                    </div>
                  </section>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section className="section p-small comparisons">
          <i className="background opacity-10 repeat" style={BG_Cream}></i>
          <div className="container-fluid">
            <div className="row justify-content-center">
              <div className="col-lg-4 col-12 col-sm-12 col-md-11">
                <div className="v-center">
                  <section className="section p-none bg-none v-center-child">
                    <div className="row justify-content-center">
                      <div className="col-lg-8 col-11">
                        <h1 className="weight-300">Case Studies</h1>
                        <p>The following are two pieces software that specializes in the creation of soundscape for Tabletop RPGs.</p>
                        <p>As a GM myself, my application came from the frustration I experienced using the available software.</p>
                        <p>While both pieces of software exists to solve the problem of queuing and playing sound effects, certain principals of their designs create limitations that I wanted to overcome.</p>
                      </div>
                    </div>
                  </section>
                </div>
              </div>
              <div className="col-12 p-3 d-lg-none"/>
              <div className="col-11 col-md-5 col-lg-4">
                <h3><a href="http://softrope.net/">Softroap<i className="fas fa-external-link-alt"></i></a></h3>
                <ul>
                  <li>Positives</li>
                  <li>Use your own audio files</li>
                  <li>Higher degree of control over audio</li>
                  <li>Multiple volume adjustments</li>
                </ul>
                <ul>
                  <li>Pain Points</li>
                  <li>Unintuitive</li>
                  <li>No focus on design</li>
                  <li>Desktop application, no mobile version</li>
                  <li>Files use system path, hard to transfer between computers</li>
                  <li>Bug Prone</li>
                </ul>
              </div>
              <div className="col-12 p-3 d-md-none"/>
              <div className="col-11 col-md-5 col-lg-4">
                <h3>Tabletop Audio<i className="fas fa-external-link-alt"></i></h3>
                <ul>
                  <li>Positives</li>
                  <li>Web application, mobile ready</li>
                  <li>Large library of high quality audio</li>
                  <li>Easy to use</li>
                  <li>Actively updated</li>
                </ul>
                <ul>
                  <li>Pain Points</li>
                  <li>Can not use your own files</li>
                  <li>Unappealing design</li>
                  <li>Arbitrarily limits amount of playable sounds</li>
                  <li>No deeper level of control over audio</li>
                </ul>
              </div>
            </div>
          </div>
        </section>
        <section className="section dark bg-sound-mixer-accent-dark considerations">
          <i className="background darken opacity-3 top" style={BG_02}></i>
          <div className="container">
            <div className="row justify-content-around">
              <div className="col-12">
                <h2 className="weight-300">Considerations</h2>
              </div>
              <div className="col-10 col-sm-5 col-lg-5">
                <div className="feature-item">
                  <h5><i className="far fa-check-square"></i>User must be able to use their own sound files</h5>
                  <p>A large limiting factor of similar apps are their reliance on provided sound libraries. Users may to use their own audio files, including music, to create personal soundscapes.</p>
                </div>
              </div>
              <div className="col-10 col-sm-5 col-lg-5">
                <div className="feature-item">
                  <h5><i className="far fa-check-square"></i>User must be able to export their completed soundscapes</h5>
                  <p>The app exports a single file that can be transferred and loaded into the app from any modern device with an internet connection and browser.</p>
                </div>
              </div>
              <div className="col-10 col-sm-5 col-lg-5">
                <div className="feature-item">
                  <h5><i className="far fa-check-square"></i>App must be device agnostic</h5>
                  <p>Any modern device must be able to access and use this app. React helps a lot in this respect. User testing is on going to reach this goal.</p>
                </div>
              </div>
              <div className="col-10 col-sm-5 col-lg-5">
                <div className="feature-item">
                  <h5><i className="far fa-check-square"></i>App must have a sound library</h5>
                  <p>The app provides a sound library of free, public domain, or MIT licensed sounds for users who don't have sound files of their own.</p>
                </div>
              </div>
              <div className="col-10 col-sm-5 col-lg-5">
                <div className="feature-item">
                  <h5><i className="far fa-check-square"></i>Most functionality must be client-side</h5>
                  <p>To reduce the need for servers, the app runs client side using React/Redux as its backbone.</p>
                </div>
              </div>
              <div className="col-10 col-sm-5 col-lg-5">
                <div className="feature-item">
                  <h5><i className="far fa-check-square"></i>Data must be accessed fast. Data-store must scale well</h5>
                  <p>Because the app will run client-side, quick data access is essential. Key-value pairs using UUIDs eliminate costly searching.</p>
                </div>
              </div>
              <div className="col-10 col-sm-5 col-lg-5">
                <div className="feature-item">
                  <h5><i className="far fa-check-square"></i>The front end must be modular and future focused</h5>
                  <p>Care was taken to write systems that allow for functionality to be added or removed with ease.</p>
                </div>
              </div>
              <div className="col-10 col-sm-5 col-lg-5">
                <div className="feature-item">
                  <h5><i className="far fa-check-square"></i>Users must have a high level of control</h5>
                  <p>Users have granular control over the sounds they import into their soundscapes. All features expected of a modern audio application are present.</p>
                </div>
              </div>
              <div className="col-10 col-sm-5 col-lg-5">
                <div className="feature-item">
                  <h5><i className="far fa-check-square"></i>Usability is essential</h5>
                  <p>The app should be as easy to pick up and use as possible, so care has been taken to implement natural, expected interactions.</p>
                </div>
              </div>
              <div className="col-10 col-sm-5 col-lg-5">
                <div className="feature-item">
                  <h5><i className="far fa-check-square"></i>Must be visually clean and well designed</h5>
                  <p>I'm a sucker for making things look nice.</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section className="section p-none">
          <div className="container-fluid">
            <div className="row justify-content-around bg-light-1">
              <div className="col-12 col-sm-11 col-lg-5 mt-4">
                <section className="section bg-none text-smaller p-xsmall">
                  <h1>Observations</h1>
                  <p>Over the time I spent programming this application I've observed and experienced a few, more abstract, things; Things that aren't so much about the act of programming as much the experience and take-away.</p>
                  <p>I've outlined a few things that I'll bring with me as I continue improving this application as well as when I move forward into other applications.</p>
                </section>
              </div>
              <div className="col-12 col-lg-5"/>
            </div>
            <div className="row justify-content-around pt-5">
              <div className="col-12 col-sm-11 col-lg-5">
                <section className="section bg-none pt-1 pb-1 pb-lg-5">
                  <div className="row justify-content-center">
                    <div className="col-12 mb-4 text-smaller">
                      <h2 className="weight-300">1. The Intention Behind React</h2>
                      <p>It is important to make an informed decision when choosing a library or framework for a project. While React is not always an appropriate choice for smaller web apps, the scope of user interactivity for this application specifically helps to justify its use.</p>
                      <p>React is very good at helping cleanly design applications with many user defined variables, and has allowed this application to have a stability and modularity that would be much harder to afford without React.</p>
                      <p>It's important to understand and appreciate the way React handles its 'virtual dom' as building the bedrock of your application with this in mind will allow you to take advantage of the way React helps to optimize larger scale applications.</p>
                    </div>
                    <div className="col-12 text-smaller">
                      <h2 className="weight-300">2. Learn The Things Outside My Wheelhouse</h2>
                      <p>To accomplish the goal of creating this versatile sound-mixing application, it was important to follow any lead I could find. I couldn't rely exclusively on what I already knew.</p>
                      <p>To make a better application, I researched sound-design, digital audio, processing, mixing, mastering. I learned enough to allow to me to make informed decisions and program features I otherwise wouldn't have thought of.</p>
                      <p>This application has spurred me to learn about music theory and production, because if I can produce my own musical tracks, I can increase the value of this application.</p>
                    </div>
                  </div>
                </section>
              </div>
              <div className="col-12 col-sm-11 col-lg-5">
                <section className="section bg-none pt-1">
                  <div className="row justify-content-center">
                    <div className="col-12 mb-4 text-smaller">
                      <h2 className="weight-300">Designs Will Change</h2>
                      <p>So far, this application went through 2 other designs before landing at its current, with each designs adapting the best elements of the previous.</p>
                      <p>Taking care to create clean HTML allowed these redesigns to be as painless as possible. Each design came with extended usability afforded to it by the code.</p>
                    </div>
                    <div className="col-12 text-smaller">
                      <h3 className="weight-300">Testing In the Field Reveals Hidden Problems and Solutions</h3>
                      <p>The first time I used this application in a live environment I went home with 10 different things to fix, change, and add.</p>
                      <p>It's easy to get trapped in your own echo-chamber, and it's not always easy to imagine every possible way the external world can effect the way your app is used.</p>
                      <p>As this app is very performance oriented, I had to consider different types of interruptions, the need for improvising, certain types of on-the-fly changes.</p>
                      <p>I found that I unconsciously want to use my app differently in a live environment than when I'm building and testing it at home, which was invaluable information.</p>
                    </div>
                  </div>
                </section>
              </div>
            </div>
          </div>
        </section>
        <section className="section p-none">
          <div className="container-fluid">
            <div className="row">
              <div className="col-12 col-lg-5">
                <div className="v-center">
                  <section className="section bg-none v-center-child">
                  <div className="row justify-content-center">
                    <div className="col-11 col-md-10">
                      <h1>Going Forward</h1>
                      <p>The amount of users actively testing this application is slowly growing, and with that will come usability tweaks and bug fixing.</p>
                      <p>Aside from that, the development roadmap looks a bit like this:</p>
                      <ul>
                        <li><p>Create "Tutorial / How To" videos, and onboarding tools.</p></li>
                        <li><p>Encourage user testing in public Tabletop RPG communities like ones found on Reddit.</p></li>
                        <li><p>Continue to add more sound-effects to the sound library.</p></li>
                        <li><p>Build/Curate a repository of pre-made soundscapes.</p></li>
                        <li><p>Google Drive integration? Load a soundscape via public Google Drive link?</p></li>
                        <li><p>Advanced features, like dynamic volume adjustment or Youtube + Soundcloud integration.</p></li>
                        <li><p>Tighten visual identity, logo design, illustrations, visual polish.</p></li>
                      </ul>
                    </div>
                  </div>
                  </section>
                </div>
              </div>
              <div className="col-12 col-lg-7 bg-dark-1">
                  <i className="background opacity-4" style={BG_03}/>
              </div>
            </div>
          </div>
        </section>
        <Footer/>
      </div>
    )
  }
}

export default SoundMixer;
